async function get(url, headers = {}) {
    return new Promise(function(resolve, reject) {
        const xhr = new XMLHttpRequest()
        xhr.open("GET", url)
        Object.keys(headers).forEach(function(header) {
            xhr.setRequestHeader(header, headers[header])
        })
        xhr.onload = function() {
            resolve(xhr.responseText)
        }
        xhr.onerror = function(e) {
            reject(e)
        }
        xhr.send()
    })
}

async function post(url, data, headers = {}) {
    return new Promise(function(resolve, reject) {
        const xhr = new XMLHttpRequest()
        xhr.open("POST", url)
        Object.keys(headers).forEach(function(header) {
            xhr.setRequestHeader(header, headers[header])
        })
        xhr.onload = function() {
            resolve(xhr.responseText)
        }
        xhr.onerror = function(e) {
            reject(e)
        }
        xhr.send(data)
    })
}

function file2dataURL(file) {
    return new Promise(function(resolve, reject) {
        const reader = new FileReader()
        reader.readAsDataURL(file)
        reader.addEventListener('load', function() {
            resolve(reader.result)
        })
        reader.addEventListener('error', function(e) {
            reject(e)
        })
    })

}

function convertCoordinate(x1, y1, x2, y2) {
    return {
        x: Math.min(x1, x2),
        y: Math.min(y1, y2),
        width: Math.abs(x1 - x2),
        height: Math.abs(y1 - y2)
    }
}

function showCover() {
    const cover = document.getElementById('global_cover')
    if (cover) {
        cover.style.display = 'block'
        return
    }
    const div = document.createElement('div')
    div.style.backgroundColor = 'black'
    div.style.opacity = '0.6'
    div.style.width = '100%'
    div.style.height = '100%'
    div.style.position = 'fixed'
    div.style.left = 0
    div.style.top = 0
    div.id = 'global_cover'
    div.style.zIndex = 500
    document.body.appendChild(div)
}
function hideCover() {
    const cover = document.getElementById('global_cover')
    if (cover) {
        cover.style.display = 'none'
    }
}

function showLoading(message = '加载中...', cover = true) {
    if (cover) {
        showCover()
    } else {
        hideCover()
    }
    const loading = document.getElementById('loading_wrapper')
    if (loading) {
        loading.style.display = 'flex'
        document.getElementById('loading_text').innerText = message
        return
    }
    const wrapper = document.createElement('div')
    wrapper.style.width = '100%'
    wrapper.style.height = '100%'
    wrapper.style.position = 'fixed'
    wrapper.style.left = 0
    wrapper.style.top = 0
    wrapper.style.display = 'flex'
    wrapper.style.justifyContent = 'center'
    wrapper.style.alignItems = 'center'
    wrapper.id = 'loading_wrapper'
    wrapper.style.zIndex = 501
    const loading_spinner = document.createElement('div')
    loading_spinner.className = 'spinner-border text-light'
    wrapper.appendChild(loading_spinner)
    const text = document.createElement('h3')
    text.innerText = message
    text.id='loading_text'
    text.style.color = 'white'
    text.style.padding = '40px'
    text.style.margin = 0
    wrapper.appendChild(text)
    document.body.appendChild(wrapper)
}

function hideLoading() {
    hideCover()
    const loading = document.getElementById('loading_wrapper')
    if (loading) {
        loading.style.display = 'none'
        document.getElementById('loading_text').innerText = '加载中...'
    }
}

export {
    get,
    post,
    file2dataURL,
    convertCoordinate,
    showCover,
    hideCover,
    showLoading,
    hideLoading,
}
