<template>
    <div class="container-fluid">
        <br>
        <div class="container">
            <form class="form">
                <div class="form-group">
                    <select class="form-control" @change="switchClass" v-model="currentSelectedClass"
                            name="currentClass" id="current_class_selector">
                        <option value="0000">点击选择你要观看的课程</option>
                        <option v-for="className in classList"
                                :key="className"
                                :value="className">{{className}}
                        </option>
                    </select>
                </div>
            </form>
        </div>
        <hr>
        <div class="container-fluid" v-if="pages !== null">
            <div class="row" style="border: 1px solid gainsboro; position: relative; height: 540px;box-sizing: border-box;overflow:hidden;">
                <div class="col-xl-3" style="height: 100%;">
                    <div style="display: flex;
                         flex-direction: column;
                         justify-content: flex-start;
                         overflow-y: scroll;
                         border-right: 1px solid gainsboro;
                         overflow-x: hidden;
                         height: 100%;">
                        <div style="display: flex;align-items: center;justify-content: center;"
                             v-for="(page,index) in pages"
                             :key="index">
                            <h6 style="text-align: center;"
                                :class="{'text-danger': index === currentPageIndex}">
                                {{index + 1}}
                            </h6>
                            <img style="margin: 5px;
                                        border: 1px solid gainsboro;"
                                 :src="'http://localhost:5500/' + currentSelectedClass + '/' + index + '.jpg'"
                                 :class="{'border-red-3px': index === currentPageIndex}"
                                 width="300" height="200" @click="switchPage(index)">
                        </div>
                    </div>
                </div>
                <div class="col-xl-9">
                    <div style="position: relative;opacity: 1;background-color: white;">
                        <img style="position: absolute;top: 0;left:200px;background-color: white;opacity: 1;"
                             width="960" height="540" alt="PPT edit background"
                             :src="'http://localhost:5500/' + currentSelectedClass + '/' + currentPageIndex + '.jpg'">

                        <canvas id="canvas_note_indicator" style="position:absolute;left:200px;"
                                class="ppt-page-canvas" width="960" height="540">
                        </canvas>

                        <div style="padding: 0;
                                    margin: 0;
                                    position: absolute;
                                    top: 0;
                                    left:200px;
                                    width: 960px;
                                    height: 540px;
                                    overflow: hidden;">
                            <span @click="playNoteAudio(`${currentPageIndex + 1}-${index + 1}`)"
                                  v-for="(note, index) in pages[currentPageIndex].pageNotes"
                                  :id="'ppt_note_item_' + index"
                                  :key="currentSelectedClass + '-' + currentPageIndex + '-' + index"
                                  style="position: absolute;
                                         font-size: 20px;
                                         cursor: pointer;
                                         padding: 2px 50px 2px 5px;
                                         border-radius: 22px;
                                         background-color: #2a93ca;
                                         opacity: 1;
                                         color: white;"
                                  :style="{
                                  left: note.x + note.width - 78 + 'px',
                                  top: note.y + note.height - 35 + 'px'}"
                                  :data-index="index"
                                  :data-x="note.x"
                                  :data-y="note.y"
                                  :data-width="note.width"
                                  :data-height="note.height">
                                <i class="fas fa-volume-up"></i>
                            </span>
                        </div>
                    </div>
                </div>
            </div>

        </div>

    </div>
</template>

<script>
    import {hideLoading, showLoading, get} from '../../../../common/src/utils'
    import {Howl} from 'howler'
    import {audioSrc} from '../mock'

    export default {
        name: 'DisplayClass',
        props: {
            studentId: {
                required: true
            }
        },
        data() {
            return {
                username: null,
                emotions: {},
                classList: [],
                pages: null,
                currentSelectedClass: '0000',
                currentPageIndex: 0,
            }
        },
        methods: {
            playNoteAudio(name) {
                name = 'voice_results/' + name + '.wav'
                console.log(name)
                const sound = new Howl({
                    src: [name],
                    format: ['mp4', 'm4a', 'wav']
                })
                sound.play()
            },
            async switchClass() {
                if (this.currentSelectedClass === '0000') {
                    this.pages = null
                    this.currentPageIndex = 0
                    return
                }
                this.pages = null
                this.currentPageIndex = 0
                showLoading()
                try {
                    let str
                    if (location.host === 'localhost:5500') {
                        str = await get(`http://localhost:5500/${this.currentSelectedClass}/emotion_results/${this.username}.txt`)
                    } else {
                        str = await get(`emotion_results/${this.username}.txt`)
                    }

                    console.log(str)
                    const emotions = {}
                    str.trim().split('\n').forEach((e) => {
                        if (e === '') {
                            return
                        }
                        console.log(e)
                        emotions[e.split(':')[0]] = e.split(':')[1]
                    })
                    console.log(emotions)

                    this.emotions = emotions
                    const data = JSON.parse(await get('http://localhost:5000/get_class_data?className=' + this.currentSelectedClass))
                    if (data.ok) {
                        this.pages = data.data.pages
                    } else {
                        alert(data.message)
                    }
                } catch (e) {
                    // eslint-disable-next-line no-console
                    console.log(e)
                    alert('课程数据加载失败, 请检查网络连接')
                }
                hideLoading()
                this.drawNoteIndicator(this.currentPageIndex)
            },
            switchPage(pageIndex) {
                this.currentPageIndex = pageIndex
                this.drawNoteIndicator(pageIndex)
            },
            drawNoteIndicator(pageIndex) {
                const self = this
                setTimeout(() => {
                    const notes = self.pages[pageIndex].pageNotes
                    const canvas = document.getElementById('canvas_note_indicator')
                    const ctx = canvas.getContext('2d')
                    ctx.clearRect(0, 0, 960, 640)
                    ctx.lineWidth = 1
                    ctx.strokeStyle = '#2a93ca'
                    let noteIndex = 1
                    for (const note of notes) {
                        const k = `${this.currentPageIndex + 1}-${noteIndex}`
                        if (this.emotions[k] === 'negative') {
                            ctx.strokeStyle = '#ca2a3f'
                        }
                        ctx.strokeRect(note.x, note.y, note.width, note.height)
                        noteIndex ++
                        ctx.strokeStyle = '#2a93ca'
                    }
                }, 300)

            },
        },
        async mounted() {
            showLoading()
            try {
                const n = parseInt(this.studentId)
                if (isNaN(n)) {
                    alert('学生学号必须为整数')
                    return
                } else {
                    this.username = 'student' + n
                }
            } catch (e) {
                alert('学生学号必须为整数')
                return
            }
            try {
                const data = JSON.parse(await get('http://localhost:5000/get_class_list'))
                if (data.ok) {
                    this.classList = data.data
                } else {
                    alert(data.message)
                }
            } catch (e) {
                console.log(e)
                alert('加载课程列表失败, 请检查网络连接状况')
            }
            hideLoading()
        }
    }
</script>

<style scoped>
    .border-red-3px {
        border: 3px solid orangered !important;
    }
</style>
