<template>
    <div class="root container">
        <div class="title">
            <h3>学生登录</h3>
        </div>
        <hr>
        <br><br>
        <div>
            <form class="form">
                <div class="form-group">
                    <label>学生姓名:</label>
                    <input v-model="username" required placeholder="请输入">
                </div>
                <br><br>
                <div class="form-group">
                    <label>学号:</label>
                    <input v-model="userId" required placeholder="请输入">
                </div>
            </form>
            <br><br>
            <button @click="login">登录</button>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'StudentLogin',
        data() {
            return {
                username: '',
                userId: ''
            }
        },
        methods: {
            login() {
                this.$emit('login', [this.username, this.userId])
            }
        }
    }
</script>

<style scoped>
    .title {
        padding-top: 40px;
    }
    input {
        width: 100%;
        color: black;
        background-color: transparent;
        border-color: transparent;
        border-bottom: 1px solid black;
        outline: none;
        font-size: 1.5em;
        padding: .5em 0;
    }
    input:focus {
        outline: none !important;
    }
    button {
        border: 1px solid black;
        background-color: transparent;
        padding: 5px 20px;
        color: black;
        border-radius: 5px;
        font-size: 1.5em;
    }
    .label {
        font-size: 16px;
        color: black;
        padding-bottom: 22px;
    }
</style>
